/* 기본적인 요소들을 리셋하여 브라우저 호환성을 높입니다 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
}

/* 메인 컨테이너 */
.App {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

/* 헤더 스타일링 */
header {
  flex-shrink: 0;
}

/* 메인 콘텐츠 영역 */
.AppContent {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow-y: auto;
}

.chart-and-adoft {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 20px;
}

.chart-container, .adoft-container {
  width: 49%; /* 두 컴포넌트를 가로로 반씩 배치 */
}

.animal-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  overflow: visible; /* 스크롤이 아닌 자연스럽게 표시 */
}

.animal-card {
  flex: 1 1 21%;
  margin: 10px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.animal-image {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.view-all {
  margin-top: 20px;
}

.view-all a {
  text-decoration: none;
  color: #007bff;
  font-weight: bold;
}

.container {
  display: flex;
  justify-content: space-between; /* 카드들 사이의 간격을 균등하게 배분 */
  align-items: stretch; /* 자식 요소들을 동일한 높이로 맞춤 */
  padding: 20px;
  gap: 20px; /* 컴포넌트 사이의 간격 */
}

@media (max-width: 768px) {
  .chart-and-adoft {
    flex-direction: column;
  }

  .chart-container, .adoft-container {
    width: 100%; /* 모바일에서 전체 너비 */
    margin-bottom: 20px; /* 컴포넌트 사이 간격 추가 */
  }

  .animal-card {
    flex: 1 1 100%;
  }
}

@media (max-width: 480px) {
  .animal-card {
    flex: 1 1 100%;
    margin: 10px 0; /* 카드 사이 간격 조정 */
  }
}
