/* App.css */

/* 기본적인 요소들을 리셋하여 브라우저 호환성을 높입니다 */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: Arial, sans-serif;
  }
  
  /* 메인 컨테이너 */
  .App {
    display: flex;
    flex-direction: column;
    height: 100vh;
  }
  
  /* 헤더 스타일링 */
  header {
    flex-shrink: 0;
    z-index: 1000;
    position: fixed;
    width: 100%;
    top: 0;
    background-color: white;
    border-bottom: 1px solid #ddd;
  }
  
  /* 메인 콘텐츠 영역 */
  .AppContent {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    overflow-y: auto;
    margin-top: 60px; /* 헤더 높이만큼 마진 추가 */
  }
  
  .chart-and-adoft {
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin-bottom: 20px;
    padding: 20px;
  }
  
  .chart-container, .adoft-container {
    width: 49%; /* 두 컴포넌트를 가로로 반씩 배치 */
  }
  
  .animal-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    overflow: visible; /* 스크롤이 아닌 자연스럽게 표시 */
  }
  
  .animal-card {
    flex: 1 1 21%;
    margin: 10px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .animal-image {
    width: 100%;
    height: auto;
    border-radius: 8px;
  }
  
  .view-all {
    margin-top: 20px;
  }
  
  .view-all a {
    text-decoration: none;
    color: #007bff;
    font-weight: bold;
  }
  
  .container {
    display: flex;
    justify-content: space-between; /* 카드들 사이의 간격을 균등하게 배분 */
    align-items: stretch; /* 자식 요소들을 동일한 높이로 맞춤 */
    padding: 20px;
    gap: 20px; /* 컴포넌트 사이의 간격 */
  }
  
  @media (max-width: 768px) {
    .chart-and-adoft {
      flex-direction: column;
    }
  
    .chart-container, .adoft-container {
      width: 100%; /* 모바일에서 전체 너비 */
      margin-bottom: 20px; /* 컴포넌트 사이 간격 추가 */
    }
  
    .animal-card {
      flex: 1 1 100%;
    }
  }
  
  @media (max-width: 480px) {
    .animal-card {
      flex: 1 1 100%;
      margin: 10px 0; /* 카드 사이 간격 조정 */
    }
  }
  
  /* Adoft.css */
  
  .adoft {
    margin: 10px;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center; /* 수직 중앙 정렬 */
    text-align: center;
    padding: 20px;
    border: 5px solid rgb(156, 179, 214);
    box-sizing: border-box;
    height: auto;
  }
  
  /* 카드 컨테이너 스타일링 */
  .card-container {
    display: flex;
    justify-content: space-between; /* 카드들 사이의 간격을 균등하게 배분 */
    margin: 20px; /* 필요에 따라 컨테이너의 바깥쪽 여백을 조정 */
  }
  
  /* 동물 카드 리스트 스타일링 */
  .animal-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 20px; /* 카드 사이의 간격 설정 */
  }
  
  /* 동물 카드 스타일링 */
  .animal-card {
    flex: 1 1 calc(20% - 20px);
    border-radius: 10px; 
    padding: 20px;
    margin-bottom: 20px; /* 아래쪽 간격 추가 */
    text-align: left;
    box-sizing: border-box; /* 패딩과 보더를 포함한 너비 계산 */
    display: flex;
    flex-direction: column;
    justify-content: space-between; /* 카드 내부 요소 간격 균등 배분 */
    min-height: 458px; /* 최소 높이 설정 */
  }
  
  /* 동물 이미지 스타일링 */
  .animal-image {
    width: 100%;
    height: auto;
    border-radius: 10px;
  }
  